import React from 'react';
import { Flex } from 'rebass';
import { Button, SelectField } from '@liveperson/lp-react-components';
import { useTranslation } from 'react-i18next';

import Logo from './Logo';

const handleClick = () => {
  window.location.href = 'https://authentication.liveperson.net/login.html';
};

const Header = () => {
  const { t, i18n } = useTranslation();

  return (
    <Flex p={[0, 0, 4, 6]} justifyContent="space-between" alignItems="center" flexWrap="wrap">
      <Logo smallSize />
      <Flex justifyContent="flex-end" flex={1} mr={3}>
        <Button
          handleClick={handleClick}
          variant="secondary"
          label={t('SignIn')}
        />
      </Flex>
      {/* NOTE: This select field is only temporary, and needs much better styling */}
      <SelectField
        value={i18n.language}
        options={[
          { name: 'English', value: 'en' },
          { name: 'Spanish', value: 'es' },
          { name: 'Japanese', value: 'ja' },
        ]}
        handleChange={(name, value) => i18n.changeLanguage(value)}
      />
    </Flex>
  );
};

export default Header;
